@import url(https://fonts.googleapis.com/css?family=Nunito:200,300,400,400i,600,600i,700,800,900);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline; }

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

textarea, select, input, button {
  outline: none; }

input[type="button" i]:active,
input[type="submit" i]:active,
input[type="reset" i]:active,
input[type="file" i]:active::-webkit-file-upload-button, button:active {
  border-style: none; }

html {
  font-size: 18px;
  font-family: 'Nunito', sans-serif;
  letter-spacing: 0.5px;
  line-height: 18px; }

body {
  background-color: #f8f9fa;
  color: #495057; }

.app {
  height: 100vh;
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column; }

h1, h2, h3, h4, h5, h6 {
  font-weight: bold; }

h1 {
  font-size: 2.1rem;
  line-height: 1.5rem; }

h2 {
  font-size: 1.75rem;
  line-height: 1.25rem; }

h3 {
  font-size: 1.4rem;
  line-height: 1rem; }

h4 {
  font-size: 1.05rem;
  line-height: 0.75rem; }

h5 {
  font-size: 0.7rem;
  line-height: 0.5rem; }

h6 {
  font-size: 0.35rem;
  line-height: 0.25rem; }

.nav {
  background: #fff;
  box-shadow: 0 0.25rem 0.125rem 0 rgba(0, 0, 0, 0.05);
  z-index: 1000; }
  .nav .title {
    font-size: 25px;
    font-weight: 700;
    color: #495057; }

.logo {
  width: 25px;
  height: 25px;
  margin-right: 15px; }

.main {
  max-width: 1100px;
  margin: 40px auto; }

.item {
  background: #495057;
  color: #fafafa;
  font-weight: 600;
  padding: 6px;
  border-radius: 4px; }
  .item.primary {
    background: #228BE6; }
  .item.success {
    background: #12B886; }
  .item.danger {
    background: #F03E3E; }

.container {
  background: none rgba(96, 125, 139, 0.1);
  border-radius: 4px; }

.section {
  border-radius: 4px;
  background: #fff;
  box-shadow: 0 0.25rem 0.125rem 0 rgba(0, 0, 0, 0.025); }

pre {
  background: none rgba(96, 125, 139, 0.1) !important;
  padding: 5px 10px !important;
  border-radius: 4px;
  margin: 0 !important; }
  pre code {
    font-size: 14px; }

.full-width {
  width: 100%; }

.full-height {
  height: 100%; }

